import axios from 'axios';
import React from 'react';

const API_KEY = 'AIzaSyAn7yYkPyVP-yfmyg2AlSlumFqstnUtUpM';

class GoogleGeocode extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Address: '',
      Zipcode: '',
      City: '',
      Location: 'placeholder',
    };

    this.geocode = this.geocode.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
  }
  geocode() {
    axios
      .get('https://maps.googleapis.com/maps/api/geocode/json', {
        params: {
          address: this.state.Address,
          key: API_KEY,
        },
      })
      .then((response) => {
        this.props.SetLocation(response.data.results[0].geometry.location);
        this.props.CheckLocation();
      })

      .catch((error) => {
        console.log(error);
      });
  }
  handleInputChange(e) {
    const target = e.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  }

  render() {
    return (
      <div>
        <input
          type="text"
          placeholder="Address"
          value={this.state.Address}
          onChange={this.handleInputChange}
          name="Address"
        />
        <input
          type="text"
          placeholder="Zipcode"
          name="Zipcode"
          value={this.state.Zipcode}
          onChange={this.handleInputChange}
        />
        <input
          type="text"
          placeholder="City"
          name="City"
          value={this.state.City}
          onChange={this.handleInputChange}
        />
        <button onClick={this.geocode}>Verify Address</button>
      </div>
    );
  }
}

export default GoogleGeocode;
