import React, { useState } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import emailjs from 'emailjs-com';
import { Wrapper, Status } from '@googlemaps/react-wrapper';

import HomeComponent from '../location/Geocode';
import './cart.css';
import GoogleGeocode from '../location/Geocode';

export default function Cart(props) {
  const [verifyLoc, SetVerifyLoc] = useState(false);
  const [viewItems, SetViewItems] = useState(true);
  //   if (!props.showCart) return null;

  function changeCartState() {
    const current = props.showCart;
    props.SetShowCart(!current);
    SetViewItems(true);
    SetVerifyLoc(false);
  }

  function dummyfunction(e) {
    e.stopPropagation();
  }

  function Purchase(e) {
    e.stopPropagation();
    const current = verifyLoc;
    const currentItems = viewItems;
    SetVerifyLoc(!current);
    SetViewItems(!currentItems);
  }

  const ModalBgAnimate = {
    initial: { opacity: 0 },
    animate: { opacity: 1, transition: { duration: 0.3 } },
    exit: { opacity: 0, transition: { delay: 0.1 } },
  };

  const CartAnimate = {
    initial: { opacity: 0, x: -100 },
    animate: { opacity: 1, x: 0 },
    exit: { opacity: 0, transition: { delay: 0.1 } },
  };

  return (
    <AnimatePresence>
      {props.showCart && (
        <motion.div
          key={'CartBG'}
          variants={ModalBgAnimate}
          initial={'initial'}
          animate={'animate'}
          exit={'exit'}
          className="cartContainer"
          onClick={changeCartState}
        >
          {viewItems && (
            <motion.div
              key={'Cart'}
              variants={CartAnimate}
              initial={'initial'}
              animate={'animate'}
              exit={'exit'}
              className="cart"
              onClick={dummyfunction}
            >
              <div className="cartItems">
                {props.product.map((item) => {
                  return <Product item={item} key={item.id} />;
                })}
              </div>
              <div className="cartCheckout">
                {props.price}
                <button onClick={Purchase}>Order</button>
              </div>
            </motion.div>
          )}
          {verifyLoc && (
            <LocationChecker
              product={props.product}
              location={props.location}
              SetLocation={props.SetLocation}
              dummyfunction={dummyfunction}
              Purchase={Purchase}
            />
          )}
        </motion.div>
      )}
    </AnimatePresence>
  );
}

class Product extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className={'productContainer'}>
        <div className="productDesContainer">
          <div className="productTitleCon">
            <h3>-{this.props.item.title}-</h3>
            <h4>Price: {this.props.item.price}</h4>
          </div>
          <p>{this.props.item.description}</p>
        </div>
        <div className="productImageContainer">
          <img
            className="productImage"
            src={this.props.item.picture}
            alt="sandwichImage"
          />
        </div>
      </div>
    );
  }
}

function LocationChecker(props) {
  const productinformation = props.product;
  const template = {};
  for (let i = 0; i < props.product.length; i++) {
    let value = `Order${[i]}`;
    Object.assign(template, { [value]: productinformation[i].title });
    console.log(value);
  }

  function CheckLocation() {
    const map = new google.maps.Map({
      center: [40.698368338470225, -73.98564910894787],
      zoom: [0],
    });

    const coordsMap: google.maps.LatLngLiteral[] = [
      { lat: 40.698368338470225, lng: -73.98564910894787 },
      { lat: 40.69866554506028, lng: -73.9568281173706 },
      { lat: 40.70428236757431, lng: -73.9481162817999 },
      { lat: 40.71193901146775, lng: -73.94047737121582 },
      { lat: 40.7304783951045, lng: -73.95150661468506 },
      { lat: 40.73586254432281, lng: -73.95508495006308 },
      { lat: 40.72955373901099, lng: -73.95920482310996 },
    ];
    const deliveryRadius = new google.maps.Polygon({
      paths: coordsMap,
    });
    deliveryRadius.setMap(map);
    if (
      google.maps.geometry.poly.containsLocation(props.location, deliveryRadius)
    ) {
      alert('check!');

      // emailjs
      //   .send(
      //     'service_1d6oo6u',
      //     'template_tqxgooo',
      //     template,
      //     'F7eoeUPRLaGd4Yx7q'
      //   )
      //   .then(
      //     (result) => {
      //       console.log(result.text);
      //     },
      //     (error) => {
      //       console.log(error.text);
      //     }
      //   );
    } else {
      alert('false!');
    }
  }
  return (
    <div className="cartLocContainer">
      <div className="cartLocation" onClick={props.dummyfunction}>
        <div className="cartHeader">
          <h3>Address Verification</h3>
          <span className="closeButtonCart" onClick={props.Purchase}>
            &times;
          </span>
        </div>
        <div className="cartLocChecker">
          <GoogleGeocode
            CheckLocation={CheckLocation}
            location={props.location}
            SetLocation={props.SetLocation}
          />
        </div>
        <div className="cartFooter"></div>
      </div>
      <div>
        <form></form>
      </div>
    </div>
  );
}
